.gym {
  display: flex;
  padding: 120px 103px 0;

  @include vp-1200 {
    flex-direction: column;
    padding: 100px 45px 0;
  }

  @include vp-767 {
    padding: 65px 0 0;
  }
}

.gym__text {
  padding: 39px 85px 0 60px;
  text-align: start;

  @include vp-1200 {
    text-align: center;
    padding: 0 88px 44px 88px;
  }

  @include vp-767 {
    text-align: start;
    padding: 0;
    padding-bottom: 34px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.gym__text h2 {
  color: #102654;
  font-family: "Alumni Sans","Arial", sans-serif;
  font-size: 48px;
  font-weight: 600;
  letter-spacing: 1.44px;
  margin: 0;

  @include vp-1200 {
    font-size: 45px;
    font-weight: 600;
    line-height: 52px;
    letter-spacing: 0.2px;
    padding-right: 16px;
  }

  @include vp-767 {
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 0.96px;
  }
}

.gym__text h3 {
  color: #102654;
  font-family: "Roboto Condensed","Arial", sans-serif;
  font-size: 16.3px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  margin: 0;
  padding-top: 8px;
  padding-bottom: 41px;

  @include vp-1200 {
    padding-top: 0;
    padding-bottom: 34px;
  }

  @include vp-767 {
    padding-top: 4px;
    padding-bottom: 26px;
  }
}

.gym__text p {
  color: #102654;
  font-family: "Roboto Condensed","Arial", sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 24.9px;
  margin: 0;

  &:last-child {
    padding-top: 3px;
  }

  @include vp-1200 {
    font-size: 13px;
    line-height: 24px;
    letter-spacing: 0.4px;
    padding-bottom: 0;

    &:last-child {
      display: none;
    }
  }

  @include vp-767 {
    padding-right: 32px;
  }
}

.gym__container {
  position: relative;
  display: flex;
  padding-bottom: 234px;
  margin: 0 auto;

  @include vp-1200 {
    justify-content: center;
    padding-bottom: 183px;
  }

  @include vp-767 {
    display: grid;
    flex-direction: column;
    padding-bottom: 70px;
  }
}

.gym__container-img {
  position: relative;

  @include vp-767 {
    padding-bottom: 40px;
  }
}

.gym__img {
  max-width: unset;
}
