.stock {
  padding: 116px 103px 120px;

  @include vp-1200 {
    padding: 78px 45px 100px;
  }

  @include vp-767 {
    padding: 0px 15px 70px;
  }
}

.stock h3 {
  color: #102654;
  font-family: "Alumni Sans","Arial", sans-serif;
  font-size: 54px;
  font-weight: 600;
  letter-spacing: 1.62px;
  margin: 0;
  text-align: center;
  padding-bottom: 54px;

  @include vp-1200 {
    font-size: 42px;
    letter-spacing: 1.26px;
    padding-bottom: 52px;
    padding-right: 9px;
  }

  @include vp-767 {
    font-size: 32px;
    letter-spacing: 0.96px;
    padding-bottom: 46px;
    padding-left: 10px;
  }
}

.stock-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 40px;

  @include vp-1200 {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 30px;
  }

  @include vp-767 {
    font-size: 32px;
    letter-spacing: 0.96px;
  }
}

.stock__deskription {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding-bottom: 73px;

  @include vp-1200 {
    padding-bottom: 0;
  }

  @include vp-767 {
    grid-template-columns: repeat(1, 1fr);
  }
}

.stock__depiction {
  background-color: #ed0233;
  color: $color-default-white;
  font-family: "Roboto Condensed","Arial", sans-serif;
  text-align: start;
  padding: 45px;

  @include vp-767 {
    padding-top: 45px;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 0;
    text-align: center;
  }
}

.stock__depiction h4 {
  font-size: 28px;
  font-weight: 700;
  line-height: 105%;
  margin: 0;

  @include vp-767 {
    font-size: 28px;
  }
}

.stock__depiction p {
  font-size: 70px;
  font-weight: 700;
  line-height: 105%;
  letter-spacing: 9px;
  margin: 0;
  padding-bottom: 14px;

  @include vp-767 {
    font-size: 71px;
    letter-spacing: 8px;
    padding-bottom: 9px;
  }
}

.stock__depiction span {
  font-size: 16px;
  font-weight: 400;
  line-height: 140%;
  padding-bottom: 198px;
  margin: 0;

  @include vp-767 {
    display: block;
    font-size: 15px;
    line-height: 170%;
    letter-spacing: 0.6px;
    text-align: center;
    padding-bottom: 0;
    padding-bottom: 38px;
    padding-left: 48px;
    padding-right: 46px;
  }
}

.stock__depiction-more {
  display: block;
  position: relative;
  font-size: 17px;
  font-weight: 700;
  letter-spacing: 2px;
  line-height: 0px;
  text-transform: uppercase;
  color: $color-default-white;
  padding-top: 199px;


  @include vp-767 {
    font-size: 15px;
    letter-spacing: 3px;
    padding-bottom: 44px;
    padding-top: 0;
  }

  &::after {
    content: "";
    display: block;
    width: 42%;
    border-bottom: 2px solid $color-default-white;
    padding-top: 8px;

    @include vp-1200 {
      width: 32%;
    }

    @include vp-767 {
      margin-left: 107px;
      width: 27%;
    }
  }

  &:hover {
    opacity: 0.8;
  }

  &:focus {
    padding: 10px;
    border: 1px solid $color-default-white;
  }

  &:active {
    padding-bottom: 5px;
    border-bottom: 2px solid $color-default-white;
  }
}

.stock__deskription-img {
  margin-left: -60px;

  @include vp-767 {
    margin-left: 0;
  }
}

.stock__deskription-foto {
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100%;
}

.stock__addition {
  color: #1c3374;
  font-family: "Roboto Condensed","Arial", sans-serif;
  text-align: start;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 40px;
  padding-bottom: 66px;

  @include vp-1200 {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
  }

  @include vp-767 {
    grid-template-columns: repeat(1, 1fr);
    text-align: center;
  }
}


.stock__addition-offer {
  padding: 35px 0 30px 31px;
  border: 7px solid;
  border-image-source: linear-gradient(148.82deg, #d10a42 0.85%, #00359f 100%);
  border-image-slice: 1;


  @include vp-767 {
    padding: 40px 45px;
  }
}

.stock__addition-offer h5 {
  font-size: 31px;
  font-weight: 700;
  line-height: 8px;
  margin: 0;

  @include vp-767 {
    font-size: 29px;
    line-height: 16px;
  }
}

.stock__addition-offer p {
  font-size: 14.3px;
  font-weight: 400;
  line-height: 140%;
  padding-top: 20px;
  padding-bottom: 80px;
  margin: 0;

  @include vp-1200 {
    padding-bottom: 57px;
  }

  @include vp-767 {
    padding-bottom: 33px;
    padding-top: 16px;
    line-height: 23px;
    letter-spacing: 0.1px;
    padding-left: 9px;
    padding-right: 9px;
  }
}

.stock__addition-offer p:last-child {
  padding-bottom: 78px;
}

.stock__addition-more {
  color: #1c3374;
  display: block;
  position: relative;
  font-size: 17px;
  font-weight: 700;
  letter-spacing: 0.7px;
  line-height: 0px;
  text-transform: uppercase;

  &::after {
    content: "";
    display: block;
    width: 13%;
    border-bottom: 2px solid #1c3374;
    padding-top: 6px;

    @include vp-1200 {
      width: 24%;
    }

    @include vp-767 {
      width: 36%;
      margin-left: 60px;
    }
  }

  &:focus {
    padding: 10px;
    border: 1px solid #1c3374;
  }

  &:active {
    color: #1c3374;
    border-bottom: 2px solid #1c3374;
  }
}
